
.section-title {
  font-weight: 500px;
  font-size: 32px;
  line-height: 100%;
  letter-spacing: -1,5;
  grid-column: span 2;

  @include on-tablet {
     grid-column: 1/4;
     font-size: 48px;
  }

  @include on-dekstop {
    grid-column: 1/4;
    font-size: 64px;
  }




  &__text {
    display: block;
    grid-column: span 2;

    @include on-tablet {
      grid-column: 4/7;
    }

    @include on-dekstop {

      grid-column: 6/11;
    }
  }
}



h2 {
  font-weight: unset;
}
